//after-images
@mixin button-colors($user-butt){
  background-color: map_get($user-butt,background-color);
  color: map_get($user-butt,color);
  position: relative;
  &::after{
    content: "";
    position: absolute;
    background: url("#{$base-url}img/icons/#{map_get($user-butt,after-images)}") no-repeat;
    width: 9px;
    height: 16px;
    top: 22px;
    right:30px;
  }
  @include on-event(){
    @if map_has_key($user-butt,hover){
      @each $property, $value in map_get($user-butt,hover){
        #{$property}: $value
      }
      &::after{
        background: url("#{$base-url}img/icons/#{map_get(map_get($user-butt,hover),after_hover-images)}") no-repeat;
      }

    }
    text-decoration: none;
    //animation: fadein ease 0.5s;
    //transition: all 0.3s ease;
    transition: all .3s ease-in-out;
  }

  @content;
}


@keyframes fadein {
  from { opacity: 0.5; }
  to   { opacity: 1; }
}

@mixin button($def-conf:()){
  $def-conf:extend((
      display: inline-block,
      width:$button-widh,
      height:$button-heading,
      font:$button-font,
      font-size:$base-font-size,
      border: $button-border,
      radius:$button-radius
  ),$def-conf);
  @each $property, $value in $def-conf{
    #{$property}: $value
  }
  padding-left: 30px;
  padding-right: 50px;
  font-weight:normal;
  line-height: map_get($def-conf,height);
  font-size: 18px;
  cursor: pointer;
  text-align: left;
  //width: auto;
  @if map_get($def-conf,radius) != none {
    border-radius: map_get($def-conf,radius);
  };
  @content
}






/***
СПИСОК КНОПОК
*/

// кнопка в сладере
.button_slide{
  @include button();
  @include button-colors($blu-butt);
}
.button_green{
  @include button();
  @include button-colors($geen-butt);
}
.button_wite{
  @include button((border: 2px solid #96D376));
  @include button-colors($wite-butt);
}
// показать ещё
.button_more{
  @include button((width:210px));
  @include button-colors($wite-butt_bottarr);
  @include after(){display: none}
  border: 2px solid #96D376;
  display:block;
  margin: 10px auto;
  svg{
    margin-left: 10px;
    position: relative;
    top: -2px;
  }
  @include on-event(){
    #button_more{
      path{
        stroke:#fff;
      }
    }
  }

}
// без стрелок с бордером
.butt_noarr--wite{
  @include button((
          width:145px,
          height:50px,
          border: 2px solid #96D376
  )){
    text-align: center;
    padding: 0;
    font-weight: 500;
    font-size: 16px;
  };
  @include button-colors($noarr-butt-wite);
}
// без стрелок серая
.butt_noarr--silver{
  @include button((
          width:145px,
          height:50px,
  )){
    text-align: center;
    padding: 0;
    font-weight: 500;
    font-size: 16px;
    border: 2px solid #fff;
  };
  @include button-colors($noarr-butt-silver);
}
//
.button-blacks{
  @include button-colors($black-butt);
  @include button((
          width:210px,
          height:50px,
          border: 2px solid #B8BABB
  )){
    font-weight: 500;
    font-size: 16px;
    &::after{
      top: 18px;
      right: 10%;
    }
  };

}