header{
  position: relative;
  z-index: 99;
  @include respond-to(lg){
    .head{
      display: none;
    }
  }
}
.head{
  background-color: $black_bg;
  color: $silver;
  &__call{
    margin: 14px 0;
    @include flex();
    span{
      @include text_size(16px,22px);
      font-weight: 500;
      margin-left: 10px;
    }
  }
  @include respond-to(md){
    &__call{
      justify-content: center;
    }
  }
  @include respond-to(small){
    &__call span{
      font-size: 14px;
    }
  }
  @include respond-to(verysmall){
    img{display: none}
  }

}
//header
.header{
  background-color: #fff;
  position: relative;
  z-index: 99;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06);
}
.header .container{
  position: relative;
  @include respond-to(small){
    .row{
      //@include flexbild(center);
      justify-content: center;
    }
  }
}

.logo_top{
  margin-top: 20px;
  a{
    display: block;
  }
  @include respond-to(sm){
    margin-top: 30px;
    img{
      width: 175px;
      height: 55px;
    }
  }
}
.avtoriz_box{
  margin-top: 35px;
}
.avtoriz{
  display: block;
  width: 146px;
  height: 50px;
  background: linear-gradient(90deg, #6FD93A 15.36%, #62C032 83.94%);
  box-shadow: inset 1px 1px 4px rgba(72, 80, 82, 0.25);
  border-radius: 30px;

  line-height: 50px;
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  font-weight: normal;
  position: relative;
  padding-left: 25px;
  vertical-align: middle;
  margin-right: 15px;
  transition: all 0.3s ease;
  &__icon{
    position: absolute;
    background-color: #fff;
    width: 38px;
    height: 38px;
    top: 6px;
    right: 6px;
    border-radius: 50%;
    text-align: center;
    #avtoriz_svg{
      position: relative;
      top: -3px;
    }
  }
  @include on-event(){
    color: #fff;
    text-decoration: none;
    #avtoriz_svg{
      path{
        stroke:#96D376;
      }

    }
  }

}
@include respond-to(lg){
  .avtoriz_box{
    flex-grow: 2;
    order: 2;
    .avtoriz{
      float: right;
    }
  }

}
@include respond-to(sm){
  .logo_top{
    flex-grow: 2;
    margin-top: 15px;
  }
  @include if_ie{
    .logo_top{
      background: url("#{$base-url}img/wite_arrleft_mob.png") left no-repeat;
      width: 175px;
      height: 55px;
      position: relative;
      z-index: 2;
      img{
        display: none;
      }
    }
  }
  .avtoriz_box{
    margin-top: 15px;
  }
}
@include respond-to(small){
  .logo_top{
    //width: 100%;
    //text-align: center;
  }
  .avtoriz_box{
    flex-grow: 0;
    .avtoriz{
      float: none;
    }
    //flex-grow:0;
  }
  .avtoriz_box .avtoriz{
    width: 50px;
    overflow: hidden;
    font-size: 1px;
    margin-right: 0;
    svg{
      margin-top:11px;
    }

  }
}
@include respond-to(verysmall){
  .logo_top img{
    margin-top: 5px;
    width: 150px;
    height: 45px;
  }
}
/// fixed
.sticky-menu{
  background-color: #fff;
  position: fixed;
  top: 0;
  width: 100%;
  height: auto;
  z-index: 99;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06);
  transition: 0.1s linear;
  .logo_top{
    margin-top: 13px;
    img{
      width: 175px;
      height: 55px;
      transition: 0.5s linear;
    }
  }
  @include if_ie{
    .logo_top{
      background: url("#{$base-url}img/wite_arrleft_mob.png") left no-repeat;
      width: 175px;
      height: 55px;
      position: relative;
      z-index: 2;
      img{
        display: none;
      }
    }
  }
  .main_menu{
    margin: 30px 0;
    margin-right: 5px;
    transition: 0.5s linear;
  }
  .avtoriz_box{
    margin-top: 15px;
    .avtoriz{margin-top: 0;}
  }
  .menu_drop{
    top: 80px;
  }
  @include respond-to(lg){
    .main_menu{
      margin-top: 13px;
      margin-bottom: 22px;
    }
  }
  @include respond-to(small){
    .logo_top{
      width: auto;
      text-align: left;
      @include if_ie{
        width: 175px;
      }
    }
    .avtoriz_box .avtoriz{
      width: 50px;
      overflow: hidden;
      font-size: 1px;
      margin-right: 0;
      svg{
        margin-top:11px;
      }

    }
  }
  @include respond-to(verysmall){
    .logo_top img{
      margin-top: 5px;
      width: 150px;
      height: 45px;
    }
  }
}

.slide{
  width: 100%;
  background-color: #F6FAFC;
  .container{
    position: relative;
    height: 100%;
  }
  &:focus{
    border: none;
    outline: none;
  }

}
.slide_top{
  .prev_cont{
    max-width: 1140px;
    margin: 0 auto;
    height: 1px;
    position: relative;
    background-color: #F6FAFC;
  }
  .slick-prev{
    position: absolute;
    top: 300px;
    left:-80px;
    z-index: 5;
  }
  .slick-next{
    position: absolute;
    top: -330px;
    right:-80px;
    z-index: 5;
  }
  @include respond-to(xl){
    .slick-prev,.slick-next,.prev_cont{display:none !important;}
  }
}
.slide__content{
  max-width: span(5);
  @include if_ie(){
    width: 50%;
    max-width: 50%;
    display: block;
    margin-top: 12%;
  }
  height: 100%;
  @include flex(center,nowrap,flex-start,column);
  &-title{
    @include responsiveText(28px,44px);
    line-height: 130%;
    font-weight: 700;
    margin-bottom: 50px;

  }
  &-text{
    @include responsiveText(18px,28px);
    line-height: 34px;
    color: #949899;
    &--small{
      @include text_size(16px,24px);
      margin-top: 20px;
    }
  }
  &-button{
    width: 250px !important;
    margin-top: 75px;
  }
  @include respond-to(md){
    max-width: span(8);
    @include if_ie(){
      width: 80%;
    }
  }
  @include respond-to(sm){
    max-width: 100%;
  }
  @include respond-to(small){
    &-button{
      margin-left: auto;
      margin-right: auto;
    }
  }

}
.host-slide{
  //background: url("#{$base-url}img/slide/host_slide_main.png")center center no-repeat;
  //background-position:right;
  background-color: #F6FAFC;
  background-size: cover;
  @include respond-to(sm){
    background-color: #485052;
  }
}
[role="main_slider"] .slide{
   height: 630px;
   background-color:$silver_bg !important;
  @include respond-to(md){
    background-size: 100% 50% !important;
    //background-position: -50% 50% !important;
  }
  @include respond-to(sm){
    height: 450px;
    background-size: 0 0 !important;
  }
 }
[role="page_slider"] .slide{
  height: 450px;
  background-color:$silver_bg !important;
  .slide__content{
    max-width: 570px;
  }
  .slide__content-text{
    width: 92%;
  }

}
[role="page_slider"] .slide_top .slick-prev{
  top:240px;
}
[role="page_slider"] .slide_top .slick-next{
  top:-210px;
}

.bg_open_slide{
  //background-color: #485052 !important;
  filter: blur(5px);
}