.columsbox_product{
  margin-top: 45px;

}
.prod_colum--mr{
  @include respond-to(lg){
    margin-bottom: 45px;
  }
}
.prod_colum{
  background: #FFFFFF;
  border: 1px solid #F1F1F2;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  section{
    padding: 0 25px;
  }
  //custom class

  &--bord{
    padding-bottom: 11px;
    border-bottom: 2px solid #F1F1F2;
  }

  /*
   tru clacc
    */
  // заголовок с тенью
  &--title{
    font-weight: 500;
    font-size: 20px;
    line-height: 60px;
    text-align: center;
    height: 60px;
    margin: 0 -25px;
  }
  // звездочка в заголовке
  .title_star{
    @include after(){
      width: 65px;
      height: 65px;
      top: -25px;
      right: 10px;
      z-index: 2;
      background: url("#{$base-url}img/star.png") no-repeat;
    }

  }
  // под заголовок по центру
  &--sub-title{
    @include text_size(16px,20px);
    text-align: center;
    margin-top: 20px;
  }
  // рандомный малеьнкий текст
  &--small-span{
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    margin-top: 20px;
  }
  /**
   табы на sll page
   */
  // протоклы на странице sll page
  &--protocl{
    margin-top: 20px;
    text-align: center;
    img{
      width: 100%;
    }
    @include respond-to(lg){
      img{
        width: auto;
      }
    }
  }
  /**
      селектор с ценой
   */
  .prod_colum__listbox{
    @include flex();
    margin-top: 20px;
    position: relative;
    .prod_colum__prices--left{
      flex-basis: 60%;
      padding: 1px 0;
    }
    .listbox{
      flex-basis: 40%;
      height: 60px;
      line-height: 60px;
      border: 2px solid #F1F1F2;
      box-sizing: border-box;
      border-radius: 0px 30px 30px 0px;
      text-align: center;
      cursor: pointer;
      .listbox_link{
        font-size: 16px;
        line-height: 20px;
        color: #949899;
        padding-right: 25px;
        background: url("#{$base-url}img/icons/arr_botom.png") right center no-repeat;
      }
    }
    .open > .listbox_link{
      background: url("#{$base-url}img/icons/arr_up.png") right center no-repeat;
    }
    .closed > .listbox_link{
      background: url("#{$base-url}img/icons/arr_botom.png") right center no-repeat;
    }
    .listbox_list{
      display: none;
      @extend %list;
      position: absolute;
      width: 100%;
      left: 0;
      top: 60px;
      z-index: 2;
      background-color: #fff;
      li{
        font-size: 14px;
        //height: 80px;
        padding: 18px 0;
        color: #949899;
        cursor: pointer;
        border-bottom: 2px solid #F1F1F2;
        span{
          margin-right: 5px;
        }
        .titl{
          display: block;
          vertical-align: middle;
          font-size: 16px;
          line-height: 20px;
          color: #949899;
        }
        .old_price{
          font-size: 14px;
          line-height: 22px;
          text-decoration-line: line-through;
          color: #6ECCE6;
        }
        &:hover{
          .titl{
            color: var(--base-color);
          }
        }


      }
    }

  }





  // список с плюсами
  &--list-dostup{
    @extend %list;
    margin: 20px;
    li{
      color: #949899;
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 20px;
    }
    .block,.dostup_plus{
      color: #CACBCC;
    }
    .dostup_plus{
      color: var(--title_crl);
    }

  }
  // цыфры большие с текстом
  &__parametrs{
    border-bottom: 2px solid #F1F1F2;
    display: flex;
    padding: 10px 0;
    .numm{
      font-size: 44px;
      line-height: 54px;
      color: #949899;
    }
    .text_param{
      text-align: left;
      font-size: 20px;
      line-height: 20px;
      margin-left: 10px;
      margin-top: 25px;
      color: #949899;
    }
  }

  // цена с рублями
  &__prices{
    border-top:  2px solid #F1F1F2;
    border-bottom: 2px solid #F1F1F2;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
    .summ{
      font-size: 44px;
      line-height: 54px;
      color: var(--title_crl);
    }
    .rubs{
      text-align: right;
      font-size: 20px;
      line-height: 20px;
      margin-left: 10px;
      color: #949899;
    }
    .tooltip{
      top: 25px;
      right: 10px;
    }
  }
  &__prices--left{
    justify-content: flex-start;
  }


  // зеленая кнопка
  &--button{
    display: block;
    width: 170px;
    margin: 20px auto;
  }
  // перечеркнутая цена
  &__price-old{
    align-self: flex-end;
    background: #F6FAFC;
    border: 3px solid #FFFFFF;
    box-sizing: border-box;
    box-shadow: inset 0px -6px 6px rgba(0, 0, 0, 0.06);
    width: auto;
    height: 60px;
    line-height: 60px;
    text-align: center;
    margin-left: -25px;
    margin-right: -25px;
    span{
      font-size: 16px;
      line-height: 20px;
      letter-spacing: 0.01em;
      text-decoration-line: line-through;
    }
    .noline{
      text-decoration-line:none;
    }
  }

}

/*
 Безлимитный тариф на на страниц хостинга
 */
.tarif_bezlimit{
  margin-top: 40px;
  .tarif_bezlimit-img{
    height: 100%;
    @include respond-to(lg){
      height: auto;
    }
  }
  .prod_colum{
    height: auto;
  }
  .prod_colum__parametrs{
    padding: 4px 0;
  }
  .prod_colum--button{
    margin-top: 0px;
    margin-bottom: 20px;
  }
  @include respond-to(md){
    .tarif_bezlimit-img{display: none}
  }
}


/**
Настройки тарифов
куча чекбоков и ценой слева
 */
.tarif_setings{
  margin-top: 35px;
}
.step_config{
  width: 100%;
  margin-bottom: 50px;
  &--title{
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #B8BABB;
    margin-bottom: 10px;
    margin-left: 20px;
  }

  &__box{
    padding: 30px 30px 20px 30px;
    background: #FFFFFF;
    border: 1px solid #F1F1F2;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
    position: relative;
    @include clearfix;
    &--name{
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      color: #485052;
      padding-bottom: 20px;
    }
    &--checbox{
      border-top: 2px solid #F1F1F2;
      padding-top: 20px;
      & > *{
        float: left;
      }
    }
    &--checbox_price{
      float: left;
      margin-top: 5px;
      margin-left: 15px;
    }

    .tooltip{
      position: absolute;
      right: 28px;
      top:33px;
    }
    .steps_box{
      padding-bottom: 25px;
    }
  }
  &--domenus{
    padding: 40px 30px;
  }
}
.serv_seting__title{
  font-weight: 500;
  font-size: 20px;
  text-align: center;
  height: 70px;
  line-height: 70px;
}
.serv_seting__box--checbox{
  @include flex();
  padding: 15px 30px;
  .checkbox{
    margin-top: 10px;
  }
  .serv_seting__box--name{
    margin-left: 0px;
    font-size: 18px;
    line-height: 22px;
  }
  .checkbox input {
    &:checked + .checkbox__text {
      color: var(--base-font);
      &:before {
        background: #45C2E1;
        box-shadow: inset 0.5px 0.5px 3px rgba(72, 80, 82, 0.25);
      }

      &:after {
        left: 16px;
      }
    }

    &:focus + .checkbox__text:before {
      outline: none;
    }
  }
}
.serv_done{
  opacity: 1;
  color: #DBDDDE !important;
  transition: all 0.3s ease;
}

/*
собраная инфа с тарифа с тарифа
 */
.you_tarif{
  padding-bottom: 30px;
  margin-top: 30px;
  background: #FFFFFF;
  border: 1px solid #F1F1F2;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
  // заголовок
  &--title{
    font-weight: 500;
    font-size: 20px;
    line-height: 60px;
    text-align: center;
    height: 60px;
  }
  // список
  &__list{
    @extend %list;
    margin: 10px 30px;
    li{
      padding: 10px 0;
      border-bottom: 2px solid #F1F1F2;
      font-size: 16px;
      line-height: 20px;
      strong{
        font-weight: normal;
      }
      span{
        color: #B8BABB;
      }
    }
  }
  // цена
  .you_tarif__summ{
    margin-top: 30px;
    text-align: center;
    span{
      font-weight: normal;
      font-size: 18px;
      line-height: 24px;
      color: #949798;
    }
    .full_sum{
      display: inline-block;
      font-weight: 500;
      font-size: 20px;
      line-height: 24px;
      color: #485052;
    }

  }
  // кнопка
  .you_tarif__butt{
    display: block;
    width: 170px;
    margin: 20px auto;
  }
}


