// чекбокс с синим
.checkbox_icons{
  padding-top: -5px;
}
.checkbox_icons input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin:10px;
}

.checkbox__text-icon {
  position: relative;
  padding: 0 0 0 30px;
  cursor: pointer;

  &:before {
    content: '';
    position: absolute;
    top: -4px;
    left: 0;
    width: 30px;
    height: 30px;
    background: url("#{$base-url}img/icons/check_def.png") no-repeat  ;
  }


}

.checkbox_icons input {
  &:checked + .checkbox__text-icon {
    &:before {
      background: url("#{$base-url}img/icons/check_blu.png") no-repeat  ;
    }


  }

  &:focus + .checkbox__text-icon:before {
    outline: none;
  }
}

/**
переключашки

 */

// переключашка с серым и зеленым
.checkbox input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
}

.checkbox__text {
  position: relative;
  padding: 0 0 0 60px;
  cursor: pointer;
  color: #949798;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;

  &:before {
    content: '';
    position: absolute;
    top: -5px;
    left: 0;
    width: 42px;
    height: 30px;
    background: #DBDDDE;
    box-shadow: inset 0.5px 0.5px 3px rgba(72, 80, 82, 0.25);
    border-radius: 30px;
    transition: .2s;
  }

  &:after {
    content: '';
    position: absolute;
    top: -1px;
    left: 4px;
    width: 22px;
    height: 22px;
    border-radius: 100px;
    background: #FFF;
    box-shadow: 0.5px 0.5px 1px rgba(72, 80, 82, 0.3);
    transition: .2s;
  }
}

.checkbox input {
  &:checked + .checkbox__text {
    color: var(--base-font);
    &:before {
      background: #6FD93A;
      box-shadow: inset 0.5px 0.5px 3px rgba(72, 80, 82, 0.25);
    }

    &:after {
      left: 16px;
    }
  }

  &:focus + .checkbox__text:before {
    outline: none;
  }
}
