/**

sekect
 */
.dropcontainer {
  position:relative;
  font-size: 16px;
  line-height: 20px;
}

.trigger {
  color: var(--base-color);
  padding: 10px 0;
  font-size: 16px;
  line-height: 20px;
  width: 100%;
  background: url("#{$base-url}img/icons/arr_botom.png") 98% center no-repeat;
  display: block;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
  @include on-event(){
    color: var(--base-color);
    text-decoration: none;
  }
}



.activetrigger {
  background: url("#{$base-url}img/icons/arr_up.png") 98% center no-repeat;
  display: block;
  color: var(--base-color);
  padding: 10px 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  @include on-event(){
    color: var(--base-color);
    text-decoration: none;
  }
}




.dropcontainer ul {
  position: absolute; 
  left: 0;
  top: 0;
  font-size: 16px;
  background: #FFFFFF;
  border: 1px solid #F4F5F5;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
  list-style-type: none;
  margin: 0;
  color: var(--base-color);
  padding: 10px;
  width: 100%;
  z-index: 2;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  a{
    color:#949899;
  }
}

.dropcontainer ul li {
  margin-bottom: 5px;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.dropcontainer ul li a:hover {
  color: #6ECCE6;
  text-decoration: none;
  outline: none;
}

.dropcontainer ul li:first-child {
  display: none;
}

.dropcontainer ul li:last-child {
  border-bottom: none;
}

.dropdownhidden {
  display: none;
}

.dropdownvisible {
  height: auto;
}
