.vs__dropdown-toggle{
  border: none !important;
}
.vs__actions{
  display: none !important;
}
.vs__selected-options{
  background: url("#{$base-url}img/icons/arr_botom.png") 98% center no-repeat;
}
.vs--open .vs__selected-options{
  background: url("#{$base-url}img/icons/arr_up.png") 98% center no-repeat;
  cursor: pointer;
}
.mysec{
  .vs__dropdown-toggle,.vs__selected-options,.vs__search{
    cursor: pointer !important;
  }
  .vs__selected{
   font-size: 16px;
  }
  .vs__dropdown-menu{
    font-size: 16px;
    background: #FFFFFF;
    border: 1px solid #F4F5F5;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
    list-style-type: none;
    margin: 0;
    color: var(--base-color);
    padding: 10px;
    li{
      font-weight: normal;
      font-size: 16px;
      padding: 0px 0;
      margin: 0;
      background: none;
      color:#949899;

    }
    li:hover {
      color: #6ECCE6;
      text-decoration: none;
      outline: none;
    }
  }
}


/**
Полхунки
 */

.steps {
  -webkit-appearance: none;
  width: 100%;
  height: 15px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;

  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
  }

  &::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
  }
}


