footer{
  background-color: #485052;
  padding-top: 60px;
  color: #808687;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  @include respond-to(small){
    .dh_mob{
      display: none;
    }
  }
}
.mr--foot{
  margin-top: 20px;
  font-size: 14px;
}
.foot_bord{
  border-right: 1px solid rgba(0, 0, 0, 0.15);
}
.footer_item{

   &__header{
     color: #B8BABB;
     font-weight: 500;
     @include responsiveText(16px,20px);
     line-height: 28px;
   }
  &__menu{
    @extend %list;
    margin-top: 20px;
    li{
      margin-bottom: 8px;
      a{
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
        color: #808687;
        @include on-event(){
          text-decoration: none;
          color: #CACBCC;
        }
      }

    }
  }
}
.foot{
  margin-top: 30px;
  padding: 30px 15px;
  border-top:1px solid rgba(0, 0, 0, 0.15);
  &__terms{
    margin-top: 10px;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
  }
  &__social{
    margin-top: 5px;
    @include flex();
    a{
      display: inline-block;
      margin-left: 18px;
    }
  }
  @include respond-to(xl){
    &__terms{
      order: 3;
      flex-grow: 2;
    }
  }
  @include respond-to(small){
    &__logo img{
      width: 120px;
      height: auto;
      margin-top: 10px;
    }
    &__terms{
      font-size: 12px;
    }
  }
}