// беграунды
.bg--mr{
  padding-bottom: 80px;
  @include respond-to(sm){
    padding-bottom: 40px;
  }
}
.bg--mr-clum{
  padding-bottom: 40px;
}
.bg--mr_dablx1{
  padding: 100px 0;
  @include respond-to(md){
    padding: 50px 0;
  }
}


.bg--silver{
  background-color: $silver_bg;
}

#overlay{
  display: none;
  position: fixed;
  z-index: 3;
  width: 100%;
  height: 100%;
  //filter: blur(5px);
  //background-color: rgba(0,0,0,0.1);
}

// отстыпы main в контентых страницах
main[role="contents"]{
  padding: 30px 0;
  position: relative;
}
// tooltip всплывающая подсказка
.tooltip{
  position: absolute;
  right: -15px;
  top: 0;
  opacity: 0.6;
  transition: all 0.3s ease;
  @include on-event(){
    opacity: 1;
  }
}
// инсайд тень в заголовкех
.inside_shadow{
  background: #F6FAFC;
  border: 3px solid #FFFFFF;
  box-sizing: border-box;
  box-shadow: inset 0px 10px 6px rgba(0, 0, 0, 0.06);
}
