.animate_register{
  overflow: hidden;
  margin-bottom: 40px;
}

.animate_register-col{
  position: relative;
  width: calc(100% * 2);
  transition: 0.3s;
  transition-timing-function: ease-in-out;
  @include flex();
  .slide-col{
    width: calc(100%/2);
  }

}
.animate-col3{
  width: calc(100% * 3) !important;
  @include flex();
  .slide-col{
    width: calc(100%/3) !important;
    margin-right: 5px;
    margin-left: 5px;
  }

}

