// media column
// акционные и предлоежние
.sale_colm{
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
  border: 1px solid #F1F1F2;
  @include media(30px){
    padding-top: 29px;
    height: 100%;
    .media_img{
      margin-top: -30px;
      img{display: block}
    }
    .media_body{
      position: relative;
      height:410px;
      @include flex($j:space-around,$dir:column);
      @include if_ie{
        display: block;
      }
    }
    .sale_colm__title{
      @include responsiveText(24px,36px);
      line-height: 42px;
      font-weight: 700;
      margin-bottom: 35px;
    }
    .sale_colm__text{
      margin-top:0;
    }
    .sale_colm__button{
      align-self: flex-end;
      margin-top: 40px;
      margin-bottom: 40px;
      margin-right: 26%;
    }
    @include respond-to(sm){
      .sale_colm__title{
        line-height: 28px;
      }
      padding-bottom: 30px;
      .media_img{
        img{display:none}
      }
    }

  }
  @include respond-to(lg){
    margin-bottom: 40px;
  }
}
/*
// preimuhesva  Преимущества iPipe
  блоки в три ряда
 */

.advansed_box{
  padding-bottom: 15px;
}
@include media_row(){
  margin-bottom: 70px;
  .media_body{
    width: 100%;
  }
  .media_body__title{
    font-weight: 500;
    @include text_size(20px,24px);
    margin-top: 10px;
    margin-bottom: 15px;
    padding:0 10px;
    span{
      position: relative;
      padding:0 10px;
    }
  }
};
.media_row__title--green{
  color:var(--title_crl);
}
.media_row__title--blu{
  color:#47AEC7;
}
/***
 меняющиеся табы с регистрацией
 */

// табы с выбором
.tabs_chang_section{
  width: 100%;
  @include flexbild(center);
}
// сами табы с цыфрами
.tabs_chang{
  @extend %list;
  @include flex();
  width: auto;
  margin-top: 40px;
  li{
    @include flex();
    margin-right: 65px;
    cursor:pointer;
    .tabs_chang--number{
      display: block;
      width: 45px;
      height: 45px;
      font-weight: 300;
      font-size: 24px;
      line-height: 40px;
      color: #ACE1F0;
      text-align: center;
      border-radius: 100px;
      background: #FFFFFF;
      border: 2px solid #ACE1F0;
    }

    .tabs_chang--text{
      display: block;
      margin-top: 10px;
      margin-left: 15px;
      font-weight: 300;
      font-size: 24px;
      line-height: 28px;
      color: #949899;
    }
    @include after(){
      background: url("#{$base-url}img/icons/line.png") no-repeat;
      width: 60px;
      height: 30px;
      right: -80px;
      top: 25px;
    }
    &:last-child{
      margin-right: 0;
      @include after(){
        background: none;
      }
    }
  }
  .active .tabs_chang--number{
    display: block;
    width: 45px;
    min-width: 45px;
    height: 45px;
    font-weight: 300;
    font-size: 24px;
    line-height: 45px;
    color: #fff;
    text-align: center;
    border-radius: 100px;
    background: #ACE1F0;
    box-shadow: inset 2px 2px 2px rgba(0, 0, 0, 0.08);
    border: none;
  }
  @include respond-to(md){
    display: block;
    li{
      margin-bottom: 25px;
      margin-right: 0;
      @include after(){display: none}
    }
  }
  @include respond-to(small){
    width: 100%;
  }
}

/***
Меняющися тарифы с чекбоком
 */
.tarifs_chang{
  margin-top: 25px;
  display: flex;
  justify-content:center;
}
.tabstarif-content{
  display: none;
}
.tabstarif-content.active{
  display: block;
}
.tarifs_box{
  @include flex();
  background: #F6FAFC;
  box-shadow: inset 0px -2px 6px rgba(0, 0, 0, 0.04);
  border-radius: 100px;
  padding: 15px perc(50px);
  position: relative;
  &__text{
    @include text_size(32px,52px);
    color: #CACBCC;
    font-weight: bold;
    cursor: pointer;
    position: relative;
    z-index: 2;
    @include respond-to(lg){
      font-size: 26px;
    }
    &:nth-child(1){
      padding-right: 65px;
    }
    &:nth-child(2){
      padding-left: 65px;
    }
  }
  .tarifs_box__active{
    color: var(--title_crl);
  }
  &_checbox{
    position: absolute;
    top: 15px;
    left: 49%;
    z-index: 1;
    transform: translate(-50%, -50%);
  }
  @include respond-to(md){
    @include flexbild(center);
    width: 80%;
    &_checbox{
      margin: 45px 0;
    }
  }
  @include respond-to(small){
    width: 100%;
  }
}

/*
  доп услуги
 */
.dop_uslugi{
  @include media_row(){
    .dop_uslugi--title{
      margin-bottom: 10px;
    }
    .dop_uslugi--price{
      color: var(--title_crl);
      margin-right: 10px;
      text-align: center;
    }
    object{margin-bottom: 20px}
    a{
      display: inline-block;
      text-align: left;
      text-decoration: underline;
      @include on-event(){text-decoration: none}
    }
    .dop_uslugi--small{
      display: block;
      font-weight: 500;
      font-size: 12px;
      line-height: 15px;
      text-align: center;
      color: #949899;

    }
  }
}
// наши клиенты
.clients{
  //margin-top: 30px;
  @include flex($j:space-around,$w:wrap);
  img{
    filter: grayscale(100%);
    margin: 0 10px;
    opacity: 0.5;
    margin-top: 30px;
    @include on-event(){
      filter: grayscale(0);
      opacity: 1;
    }
  }
  @include respond-to(sm){
    flex-wrap: wrap;
    justify-content: space-around;
  }
}
.clients_bord{
  margin-top: 40px;
  border: 2px solid #EFF6F9;
  height: 2px;
  width: 100%;
}
// faq
.foqbox{
  @extend %list;
  margin-top: 60px;
  .quest{
    font-weight: 500;
    border-top: 2px solid #EDEDED;
    padding: 15px 0;
    padding-left: 40px;
    cursor: pointer;
    background: url("#{$base-url}img/icons/arr_botom.png") left 14px center no-repeat;
  }
  .return{
    padding-left: 40px;
    padding-bottom: 20px;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    display: none;
  }
  .quest.open{
    color: var(--title_crl);
    background: url("#{$base-url}img/icons/arr_up.png") left 14px center no-repeat;
  }
}
/**
 бесплатные опции
 */
.free-images{
  width: 100%;
  height: auto;
  @include respond-to(md){
    display: none;
  }
}
.free-box{
  margin-top: 70px;
  &--title{
    position: relative;
    z-index: 2;
    @include text_size(18px,24px);
    text-align: center;
    padding-top: 25px;
    padding-bottom: 15px;
    background: #FFFFFF;
    color: #808687;
    border: 3px solid #FFFFFF;
    box-sizing: border-box;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06), inset 0px 10px 6px rgba(0, 0, 0, 0.06);
  }
  &__list{
    position: relative;
    z-index: 2;
    @extend %list;
    margin-top: 60px;
    li{
      margin-bottom: 40px;
      @include flex();
      span{
        margin-left: 15px;
        margin-top: 5px;
      }
    }
  }
  .free_lines{
    position: relative;
    z-index: 1;
    &::before{
      content: "";
      display: block;
      position: absolute;
      width: 180px;
      height: 10px;
      left: -175px;
      top: 40px;
      z-index: 1;
    }
    &::after{
      content: "";
      display: block;
      position: absolute;
      width: 2px;
      height: 412px;
      left: 25px;
      bottom: -400px;
      z-index: 1;
    }
  }
  // adaptiv
  @include respond-to(md){
    margin-top: 10px;
    &--title{
      text-align: left;
      padding-left: 10px;
    }
  }


  .free_lines--wite{
    &::before{
      background: url("#{$base-url}img/icons/free1_wite.png") no-repeat;
    }
    &::after{
      background: url("#{$base-url}img/icons/free2_wite.png") no-repeat;
    }
  }
  .free_lines--black{
    &::before{
      background: url("#{$base-url}img/icons/free1_black.png") no-repeat;
    }
    &::after{
      background: url("#{$base-url}img/icons/free2_black.png") no-repeat;
    }
  }
}

/*
тестиг
 */
.testing_box{
  border: 3px dashed #DBDDDE;
  background: #fff;
  width: 100%;
  border-radius: 100px;
  section{
    margin: 10px;
    border-radius: 100px;
    background: #F6FAFC;
    box-shadow: inset 0px -6px 10px rgba(0, 0, 0, 0.04);
    width: auto;
    height: 100%;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    @include between;
    @include respond-to(md){
      border-radius: 30px;
      @include flexbild(center);
      text-align: center;
    }
  }
  &--wite{
    background: #FFFFFF !important;
    box-shadow: inset 0px -6px 10px rgba(0, 0, 0, 0.04) !important;
  }
  &__text{
    padding: 28px 0;
    padding-left: perc(65px);
  }
  .testing_box__butt{
    min-width: 205px;
    margin: 10px;
  }
  @include respond-to(md){
    border-radius: 30px;
    &__text{
      padding: 15px;
    }
  }
}