//
.left_bar{
  display: block;
  @include respond-to(lg){
    width: 320px;
    display: none;
    position: absolute;
    top: 130px;
    left: 0;
    background-color: #fff;
    z-index: 3;
    box-shadow:4px 4px 4px rgba(0,0,0,0.06);
  }
  @include respond-to(sm){
    top: 200px;
  }
}
.left_bar__scroll-box{
  position: sticky;
  top: 120px;
}



.contents_mob{
  .open_cats{
    display: none;
  }
  @include respond-to(lg) {
    @include flex($w:wrap);
    .open_cats {
      display: none;
      margin-top: 6px;
      margin-right: 10px;
      border: 2px solid #96D376;
      box-sizing: border-box;
      border-radius: 30px;
      height: 50px;
      width: 180px;
      padding-left: 40px;
      padding-right: 25px;
      line-height: 45px;
      font-weight: 500;
      font-size: 18px;
      text-align: left;
      color: #62C032;
      background-color: #fff;
      cursor: pointer;
      background: url("#{$base-url}img/icons/right-arr-green.png") left 15px center no-repeat;
    }
    .open_cats.open {
      background: url("#{$base-url}img/icons/wite_arrbot.png") left 15px center no-repeat;
      background-color: #62C032;
      color: #fff;
    }
    @include respond-to(sm) {
      display: block;
    }
  }
}

// категории
.categories{
  margin-top: 10px;
  margin-bottom: 30px;
  background: #F6FAFC;
  border: 1px solid #E3F2D9;
  &__header{
    width: 100%;
    height: 60px;
    line-height: 60px;
    font-size: 20px;
    font-weight: 600;
    text-align: center;
    background-color: #fff;
    @include after(){
      background: url("#{$base-url}img/shadow_advantages.png") bottom left no-repeat;
      bottom: -12px;
      left: 0;
      width: 100%;
      height: 12px;
    }
  }
}
.cats-acord{
  @extend %list;
  margin-top: 15px;
  margin-left: 25px;
  &__item{
    margin-bottom: 25px;
    padding-left: 20px;
    padding-right: 15px;
    &--link{
      @include text_size(18px,25px);

      color: #949899;
      font-weight: normal;
      @include on-event(){
        color: var(--base-color);
        text-decoration: none;
      }
      @include after(){
        background: url("#{$base-url}img/icons/arr_left_low.png") left center no-repeat;
        width: 8px;
        height: 12px;
        left: -20px;
        top: 5px;
      }

    }
  }
  // под категории
  &__sub{
    display: none;
    @extend %list;
    margin-top: 20px;
    margin-left:17%;
    padding-bottom: 30px;
    &-item{
      margin-bottom: 20px;
      .sub-item--link{
        display: block;
        color: #949899;
        @include text_size(16px,20px);
        @include on-event(){
          color: var(--base-color);
          text-decoration: none;
        }
      }
    }
  }
  // при активеном
  .slide_menu{
    @include after(){
      background: url("#{$base-url}img/shadow_advantages.png") bottom left no-repeat;
      bottom: 0px;
      left: -25px;
      width: 100%;
      height: 12px;
    }
    .cats-acord__item--link{
      color: var(--base-color);
      @include after(){
        background: url("#{$base-url}img/icons/arr_green_botom.png") no-repeat;
        width: 14px;
        height: 8px;
        left: -25px;
      }
    }
    .cats-acord__sub{
      display: block;
    }
    .cats-acord__sub--active{
      color: var(--title_crl);
      font-weight: 500;
    }
    &:last-child{
      @include after(){
        background: none;
      }
      .cats-acord__sub{
        margin-bottom: 0 !important;
        padding-bottom: 0 !important;
      }

    }
  }
}

// baners
.baner_small{
  display: block;
  margin-bottom: 30px;
  width: 100%;
  background: #F6FAFC;
  border: 1px solid #E3F2D9;
  border-radius: 0px 0px 50px 0px;
  text-align: center;
  color: var(--base-color);
  position: relative;
  .baner_small-link{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
  }
  // заголовок и заголовок с зеленым
  .baner_small__item-title{
    font-weight: 500;
    @include text_size(20px,24px);
    span{
      display: block;
    }
  }
  // изображение
  .baner_small__item-images{
    width: 100%;
    img{
      display: block;
      width: 100%;
      max-height: 180px;
      border-radius: 0px 0px 50px 0px;
    }
    @include after(){
      background: url("#{$base-url}img/shadow_advantages2.png") bottom left no-repeat;
      bottom: -12px;
      left: 0;
      width: 100%;
      height: 12px;
    }
  }
  // белый фон
  .baner__item--wite{
    background: #FFFFFF;
    border-radius: 0px 0px 50px 0px;
  }
  // отступы верхний блок
  .baner__item--top{
    padding: 30px 0;
  }
  // отступы нижний блок
  .baner__item--bottom{
    padding: 20px 0;
  }
  // блок в котором кнопка
  .baner__item--button{
    padding-top: 40px;
    padding-bottom: 30px;
  }
  // добавление нижней теней
  .baner__item--afterbg{
    @include after(){
      background: url("#{$base-url}img/shadow_advantages.png") bottom left no-repeat;
      bottom: -12px;
      left: 0;
      width: 100%;
      height: 12px;
    }
  }
}

/// основныое страниц
.contents_title{
  padding-top: 0;
  text-align: left;
}
// хлебные
.breadcrumbs{
  margin-top: 15px;
  a{
    display: inline-block;
    margin-right: 26px;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.01em;
    text-decoration-line: underline;
    color: #B8BABB;
    position: relative;
    &::after{
      position: absolute;
      content: "-";
      right: -16px;
      margin-left: 10px;
      text-decoration: none;
    }
    @include on-event(){
      text-decoration: none;
    }
  }
  .active{
    text-decoration: none;
    @include after(){display: none}
  }
}
// поиск
.contens_serch{
  margin-top: 25px;
  margin-bottom: 20px;
  background: #FFFFFF;
  border: 2px solid #E4E6E6;
  box-sizing: border-box;
  border-radius: 100px;
  width: 100%;
  form{
    width: 100%;
    @include between;
  }
  input[type="text"]{
    border: none;
    padding-left: 30px;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    height: 60px;
    line-height: 60px;
    color: #B8BABB;
    border-radius: 100px;
    flex-grow: 2;
    font-family: var(--base-font);
    @include on-event(){
       outline: none;
     }
    &::placeholder{
      color: #D2D3D4;
    }
  }
  input[type="button"]{
    border: 2px solid #96D376;
    box-sizing: border-box;
    border-radius: 30px;
    margin: 5px;
    height: 50px;
    width: 145px;
    font-family: var(--base-font);
    line-height: 45px;
    font-weight: 500;
    font-size: 18px;
    padding-left: 35px;
    text-align: left;
    padding-right: 30px;
    color: #62C032;
    background-color: #fff;
    cursor: pointer;
    transition: all 0.3s ease;
    background: url("#{$base-url}img/serch.png") right 25px center no-repeat;

    @include on-event(){
      background: url("#{$base-url}img/icons/serch_wite.png") right 25px center no-repeat;
      background-color:#62C032;
      color: #fff;
      outline: none;
    }
    @include respond-to(small){
      width: 50px;
      border-radius: 100px;
      padding: 0;
      font-size: 0px;
      background-position: center center;
      @include on-event(){background-position: center center;}
    }


  }
}

// основной контент
.contents{
  padding-top: 30px;

}
.contents-section{
  width:100%;
  section{
    position: relative;
    @include flex();
    padding: 40px;
    margin-bottom: 50px;
    transition: all 0.5s ease;
    border: 1px solid #F1F1F2;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
    .section-link{
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 2;
    }

    .contents-section__images{
      text-align: center;
      img{
        display:block;
        max-width: 120px;
        max-height: 120px;
      }
    }
    .contents-section--link{
      display: none;
      margin-top:5px;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      text-align: center;
      text-decoration-line: underline;
      color: #6ECCE6;
      @include on-event(){
        text-decoration: none;
      }
    }
    .contents-section__content{
      margin-left: 30px;
    }
    .contents-section--title{
      font-weight: 600;
      font-size: 20px;
      line-height: 26px;
    }
    .contents-section--date{
      font-weight: 500;
      font-size: 12px;
      line-height: 15px;
      color: #B8BABB;
      margin-top: 16px;
    }
    // при наведении на section
    @include on-event(){
      background: #FFFFFF;
      box-shadow: 1px 1px 30px rgba(0, 0, 0, 0.15);
      .contents-section--link{display: block}
    }
    @include respond-to(sm){
      border: none;
      box-shadow: none;
      padding: 3%;
      p{
        @include text_size(14px,22px)
      }
    }
    @include respond-to(small){
      .contents-section__images{
        display: none;
      }
      .contents-section__content{
        margin-left: 0;
      }
    }

  }
}
