//
.list_nums{
  counter-reset: myCounter;
  padding-left: 0;
  li{
    list-style: none;
    padding-left: 60px;
    position: relative;
  }

  li:before {
    counter-increment: myCounter;
    content:counter(myCounter);
    background: none;
    position: absolute;
    left: 0;
    top: 10px;
    text-align: center;
    width: 40px;
    height: 40px;
    border: 2px solid #ACE1F0;
    border-radius: 30px;
    font-weight: 500;
    font-size: 18px;
    line-height: 40px;
    color: #ACE1F0;

  }
}


// банер большой
.baner_big{
  margin-top: 50px;
  width: 100%;
  border: 1px solid #E3F2D9;
  background-color: #F6FAFC;
  position: relative;
  @include clearfix;
  .baner_big--link{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
  }
  &__content--size{
    width: 350px;
  }
  &__head{
    width: 100%;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    background: #FFFFFF;
    border: 1px solid #E3F2D9;
    text-align: center;
    padding: 35px 15%;
    @include after(){
      background: url("#{$base-url}img/shadow_advantages.png") top left no-repeat;
      bottom: -12px;
      left: 0;
      width: 100%;
      height: 12px;
      z-index: 3;
    }
  }
  &__title{
    margin-top: 30px;
    margin-bottom: 30px;
  }
  .left_content{
    float: left;
    .baner_big__head{
      border-radius: 0px 0px 50px 0px;
    }
    .baner_big__title{
      margin-left: 15%;
    }
  }
  .right_content{
    float: right;
    .baner_big__head{
      border-radius: 0px 0px 0px 50px;
    }
    .baner_big__title{
      margin-right: 15%;
    }
    .baner_big__head{
      @include after(){
        background: url("#{$base-url}img/bunner/shadow_advantagesright.png") top right no-repeat;

      }
    }
  }
  @include respond-to(small){
    background: none !important;
    background-color: #F6FAFC !important;
    &__content--size{
      width: 100%;
    }
    .baner_big__title{
      margin-left: 5% !important;
      margin-right: 5%;
      text-align: center;
    }
  }
}
