// регистрация выбраные доменны c кнопкой
.chanch_domen{
  background-color: #485052;
  padding: 25px;
  margin-top:40px;
  &--title{
    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
    color: #FFFFFF;
    margin-top: 10px;
  }
  @include respond-to(sm){
    margin-top: 0px;
    &--title{
      text-align: center;
      margin-bottom: 15px;
    }
  }
}



// поиск домена
.serch_domen-section{
  width: 100%;
  background: #FFFFFF;
  border: 1px solid #F1F1F2;//#F1F1F2
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
  padding: 40px;
  padding-bottom: 70px;
  margin-top: 65px;
  @include respond-to(xl){
    padding: 10px;
  }
  &--button{
    position: absolute;
    display: block;
    left: 0;
    right: 0;
    margin:35px auto;
  }

}
.domens_padd{
  padding-bottom: 100px !important;

}

// поиск
.domen_serch{
  //margin-top: 25px;
  margin-bottom: 20px;
  background: #FFFFFF;
  border: 2px solid #E4E6E6;
  box-sizing: border-box;
  border-radius: 100px;
  width: 100%;
  form{
    width: 100%;
    @include between;
  }
  input[type="text"]{
    border: none;
    padding-left: 30px;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    height: 60px;
    line-height: 60px;
    color: #B8BABB;
    border-radius: 100px;
    flex-grow: 2;
    font-family: var(--base-font);
    @include on-event(){
      outline: none;
    }
    &::placeholder{
      color: #D2D3D4;
    }
    @include respond-to(small){
      flex-grow: 1;
      width: 50%;
    }
  }
  input[type="button"]{
    border: 1px solid #62C032;
    box-sizing: border-box;
    border-radius: 30px;
    margin: 5px;
    height: 50px;
    width: 145px;
    line-height: 50px;
    font-size: 18px;
    padding-left: 35px;
    text-align: left;
    padding-right: 30px;
    background-color: #fff;
    font-weight: normal;
    cursor: pointer;
    transition: all 0.3s ease;
    font-family: var(--base-font);
    background: url("#{$base-url}img/icons/serch_wite.png") right 25px center no-repeat;
    background-color:#62C032;
    color: #fff;

    @include on-event(){
      color: #fff;
      background-color:#0099CC;
      border: 1px solid #0099CC;
      outline: none;
    }

    // respot
    @include respond-to(small){
      width: 50px;
      border-radius: 100px;
      padding: 0;
      font-size: 0px;
      background-position: center center;
      @include on-event(){background-position: center center;}
    }


  }
}
// список доменов
.domen_result{
  @extend %list;
  li{
    @include flex();
    margin-bottom: 30px;
    .whois{
      border:2px solid #F6FAFC !important;
      @include on-event(){
        color: var(--base-font);
        background:#F6FAFC !important;
        border:2px solid #F6FAFC !important;
      }
    }
    @include respond-to(sm){
      display: block;
      .domen_result__item{
        width: 100%;
      }
    }
  }
  &--blocks{
    background: none !important;
  }
  &__item{
    width:calc(100% - 180px);
    background: #F6FAFC;
    border-radius: 30px;
    padding:15px 25px;
    height: 50px;
    @include between;

    .domen_title{
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      flex-grow: 1;
      flex-basis: 40%;
    }
    section{
      flex-grow: 1;
      flex-basis: 50%;
      @include between;
      .domen_item--block{
        margin-left: 20px;
      }
    }
      .domen_item--free{
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: #86CD62;
      }
      .domen_item--block{
        font-size: 16px;
        line-height: 20px;
        color: #949899;
        display: block;
      }
      .domen_item--price{
        font-size: 16px;
        line-height: 20px;
      }
    //whois


    // respot
    @include respond-to(md){
      display: block;
      height: auto;
      section{
        margin-top: 10px;
      }
    }
    @include respond-to(small){
      .checkbox_icons{
        display: none !important;
      }
    }

  }
  &__butt{
    display: block;
    margin-left: 30px;
    font-weight: normal;
    @include respond-to(md){
      margin-top: 30px;
    }
    @include respond-to(sm){
      margin-top:0;
      margin-left: 20px;
    }
  }
}


//// выбор доменомв
.domen_zones{
  margin: 10px 0;
  @include respond-to(lg){
    margin:20px 0;
    padding: 20px;
    background-color: #F6FAFC;
    border-bottom: 2px solid #F6FAFC;
    .domen_zones--chek{
      margin-bottom: 15px;
    }
  }
}

.tabs__caption{
  @extend %list;
  margin-top: 5px;
  li{
    //height: 30px;
    margin-bottom: 25px;
  }
  @include respond-to(lg){
    @include flex($j:space-around,$w:wrap);
    li{
      width: 40%;
    }
  }
  @include respond-to(sm){
    padding-left: 15px;
    li{
      width: 100%;
    }
  }
}
.tabs__content {
  display: none;
  section{
    @include flex($j:space-between,$w:wrap);
  }
}
.tabs__content.active {
  display: block;
}

.domen_chanc__box{
  width: 46%;
  //margin-left: 15px;
  //margin-right: 15px;
  //margin-top: 10px;
  margin-bottom: 19px;
  @include respond-to(lg){
    width: 50%;
    padding:0 2%;
  }
  @include respond-to(md){
    width: 80%;
  }
  @include respond-to(sm){
    width: 100%;
    padding: 0;
    .checkbox__def-text{
      display: block !important;
    }
    .domen_chanc__right{
      margin-left: 15px;
    }
  }
}

// блоки с чекбоксом и ценой
.domen_chanc{
  width: 100%;
  .checkbox__def-text{
    @include between;
  }
  .domen_chanc__left{
    flex-grow: 1;
    flex-basis: 40%;
    margin-left: 15px;
    border-bottom: 1.5px dashed #F1F1F2;
  }
  .domen_chanc__right{
    flex-grow: 1;
    flex-basis: 50%;
    @include between;
    border-bottom: 1.5px dashed #F1F1F2;
  }
    &--price-old{
      margin-top: 5px;
      font-size: 14px;
      line-height: 22px;
      text-decoration-line: line-through;
      color: #6ECCE6;
    }
    &-price{

    }
}
