/**
 * Basic typography style for copy text

 @@@@@@@@@@@@@@

                font-family:$def-font,
                font-size:2em,
                font-weight:normal,
                text-transform:uppercase,
                text-align: center,
                color: map-get($colors, blue),
                margin:0

*/


$headings-complex: (
        h1: (
                font-family:$base-font,
                font-weight:700,
                text-align:center,
                font-size:36,
                line-height: 42px,
                padding-top:80px,
                margin:0
        ),
        h2: (
                font-family:$base-font,
                font-weight:700,
                text-align:center,
                font-size:36px,
                line-height: 42px,
                padding-top:80px,
                margin:0
        ),
        h3: (
                font-family:$base-font,
                font-weight:600,
                text-align:center,
                font-size:28px,
                line-height: 34px,
                padding-top:50px,
                margin:0
        ),
        h4: (
                font-family:$base-font,
                font-size:20px,
                line-height: 24px,
                font-weight:600,
                margin:0
        ),

);

@each $element, $style-map in $headings-complex {
  #{$element} {
    @each $property, $value in $style-map {
      #{$property}: $value;

    }
  }
  @include respond-to(md){
    h1,h2{
      font-size: 30px;
      padding-top: perc(100px);
    }
  }
  @include respond-to(sm){
    h1,h2{
      padding-top: 40px;
    }
  }
  @include respond-to(small){
    h1,h2{
      font-size: 26px;
      line-height: 32px;
    }
  }
}
// ситили заголовков
.title--left{
  text-align: left;
  position: relative;
  z-index: 2;
}
.title--green{
  color: var(--title_crl);
}
.title_500{
  font-weight: 500;
}

.title--mr_top{
  padding-top: 58px;
}
.title--mr_bottom{
  margin-bottom: 58px;
}
.title--mr_media{
  margin-bottom: 38px;
}




// поведение параграфов
.paragraf--mr{
  margin-top: 35px;
}

%list{
  list-style: none;
  margin: 0;
  padding: 0;
}

.list--nopad li{
  font-size: 18px;
  line-height: 28px;
  margin-bottom: 10px !important;
}
