// регистарцая
.form_box{
  border: 1px solid #F1F1F2;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
  margin-top: 45px;
  padding-bottom: 70px;
  background-color: #fff;
  h3{
    padding: 25px 0;
  }
  .forms{
    margin-top: 55px;
    padding: 0 40px;
  }
  @include respond-to(small){
    .forms{
      padding: 0 15px;
    }

  }
}
.form_grop{
  margin-bottom: 40px;
  label{
    display: block;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    margin-left: 30px;
    margin-bottom: 10px;
  }
  &__input{
    display: block;
    width: 100%;
    border: 2px solid #E4E6E6;
    box-sizing: border-box;
    border-radius: 100px;
    padding-left: 30px;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    height: 60px;
    line-height: 60px;
    color: #B8BABB;
    flex-grow: 2;
    font-family: var(--base-font);
    &:focus{
      outline: none;
      border: 2px solid #CAEBF5;
    }
    &:required{
      outline: none;
      border: none;
      //border: 2px solid #E1A2A2;
    }
    &::placeholder{
      color: #B8BABB;
    }
  }

  // телефон
  .form_grop--phone{
    padding-left: 100px;
  }
}
// блок с кнопкой лона
.logins_grop{
  background: #F6FAFC;
  box-shadow: inset 0px -2px 6px rgba(0, 0, 0, 0.04);
  border-radius: 100px;
  vertical-align: middle;
  margin-top: 70px;
  width: 100%;
  height: 60px;
  line-height: 60px;
  span{
    display: inline-block;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    margin-left: 36px;
    color: $form;
  }
  &--button{
    float: right;
    width: 145px;
    padding-left: 35px;
    @include respond-to(lg){
      margin-top: 10px;
    }
  }

  @include respond-to(md){
    margin: 30px 0;
    &--button{
      display: inline-block;
      margin-top: 0;
    }
  }
  @include respond-to(sm){
    text-align: center;
    height: auto;
    padding: 20px 10px;
    border-radius: 30px;
    span{
      display: block;
      margin-left: 0;
    }
    &--button{
      float: none;
      margin-top: 20px;
    }
  }
}
// блокс согласием и кнопкой
.submit_grop{
  margin-top: 70px;
  @include between;
  .checkbox_icons{
    flex-basis:calc(100% - 200px);
  }
  .checkbox__text-icon{
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    color: #D2D3D4;
    padding-left: 40px;
    &::before{
      top:6px;
    }
  }
  input[type="submit"]{
    border: 1px solid #62C032;
    box-sizing: border-box;
    border-radius: 30px;
    height: 60px;
    width: 170px;
    line-height: 55px;
    font-weight: 500;
    font-size: 18px;
    padding-left: 35px;
    text-align: left;
    padding-right: 30px;
    background-color: #fff;
    cursor: pointer;
    transition: all 0.3s ease;
    font-family: var(--base-font);
    background: url("#{$base-url}img/icons/wite_arrleft.png") right 25px center no-repeat;
    background-color:#62C032;
    color: #fff;

    @include on-event(){
      background: url("#{$base-url}img/icons/butt_arr-green.png") right 25px center no-repeat;
      background-color:#fff;
      color: #62C032;
      border: 1px solid #62C032;
      outline: none;
    }


  }
  @include respond-to(lg){
    display: block;
    input[type="submit"]{
      float: right;
      margin-top: 23px;
    }
  }
}