/// new products
.products{
  margin-bottom: 70px;
}
.products__main{
  display: block;
  width: 100%;
  margin-top:48px;
  border: 1px solid #F1F1F2;
  padding: 30px 35px;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
  position: relative;
  @include on-event(){
    text-decoration: none;
    box-shadow: 1px 1px 30px rgba(0, 0, 0, 0.15);
    .products__head{
      background-image: url("#{$base-url}img/product_tabs/arr_head_hover.png");
    }
  }
  .products_link{
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2;
  }

  @include respond-to(lg){
   .products__main-img img{
      width: 100%;
      height: auto;
    }
  }
  @include respond-to(md){
    display: none;
  }
}
.products__head{
  @include responsiveText(18px,28px);
  line-height: 34px;
  width: 100%;
  background: url("#{$base-url}img/product_tabs/arr_head.png") center right no-repeat;
}
.products__main-separator{
  margin-top:37px;
  margin-bottom: 50px;
  width: 100%;
  height: 9px;
  background: url("#{$base-url}img/product_tabs/product_separator.png") center no-repeat;
}
.products__main-content{
  .products_content__list{
    @extend %list;
    li{
      background: url("#{$base-url}img/product_tabs/arr.png") left center no-repeat;
      padding-left: 20px;
      margin-bottom: 12px;
      a{
        color: $sv;
        font-weight: normal;
        @include on-event(){
          color: $black_bg;
          text-decoration: none;
        }
      }
    }
  }
}
.products_list{
  margin-top: 30px;
  a{
    display: block;
    padding: 30px 35px;
    border: 1px solid #F1F1F2;
    font-weight: normal;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
    margin-bottom: 30px;
    //max-height: 350px;
    @include on-event(){
      text-decoration: none;
      box-shadow: 1px 1px 30px rgba(0, 0, 0, 0.15);
      .products__head{
        background-image: url("#{$base-url}img/product_tabs/arr_head_hover.png");
      }
    }
  }
  .products__sect-img{
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .products__head{
    color: $black_bg;
    @include responsiveText(22px,28px);
    margin-bottom: 20px;
  }
  .products__img{
    display:block;
    width: 100%;
    //max-width: 250px;
    max-height: 250px;
  }
  // скрывает первый а на мд показывает
  .products-main-list{
    display: none;
  }

  @include respond-to(lg){
    @include on-event(){
      .products__head{
        background-image:none !important;
      }

    }
    .products__head{
      //background: none;
      background-image:none;

    }
  }
  @include respond-to(md){
    .products-main-list{
      display: block;
    }

  }
  @include respond-to(sm){
    padding: 10%;
    .products__img{
      max-height: 300px;
    }
  }
  @include respond-to(small){
    padding: 3%;
  }
}

// about
.bg--about{
  background: url("#{$base-url}img/bg/about.jpg") center center no-repeat;
  padding-bottom: 107px;
  h2{
    margin-bottom: 25px;
  }
  @include respond-to(md){
    background-size: 0 0;
    background-position: right;
    padding-bottom: 50px;
  }
}


// коментарии
.slider_comments{
  margin-top: 60px;
  margin-bottom: 20px !important;
  padding: 48px 60px;
  padding-bottom: 70px;
  background-color: #fff;
  border: 1px solid #F4F5F5;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
  .slick-slide:focus{
    border: none;
    outline: none;
  }
  &__header{
    @include between;
    @include media(8%){
      max-width: span(8);
      padding: 0;
      .media_img{
        min-width: 120px;
        height: auto;
        @include if_ie{
          max-width:120px;
          width: 120px;
        }
      }
      .comments_autor__title{
        //@include text_size(20px,26px);
        @include responsiveText(16px,20px);
        line-height: 26px;
        font-weight: 600;
        margin-top: 5px;
      }
      .comments_autor__text{
        margin-top: 23px;
        font-weight: 600;
        color: #949899;
        letter-spacing: 0.01em;
        @include responsiveText(15px,16px);
        line-height: 22px;
      }
      //respon
      @include respond-to(lg){
        max-width: span(10);
        .media_img{
          width: 95px;
          height: 85px;
          min-width: 95px;
          margin-top: 10px;
        }
      }
      @include respond-to(sm){
        max-width: 100%;
      }
      @include respond-to(small){
        .media_img{
          display:none;
        }
      }

    };
    .comments_header__logo{
      margin-top: 5px;
    }
    @include respond-to(lg){
      .comments_header__logo{
        float: right;
        width: 15%;
        img{
          width: 100%;
          height: auto;
        }
      }
    }
    @include respond-to(sm){
      .comments_header__logo{
        display: none;
      }

    }
    @include respond-to(small){
      .media .media_body{
        margin-left: 0;
      }
    }
  }
  &__content{
    padding-top: 0px;
    @include respond-to(md){
      font-size: 14px;
      line-height: 22px;
    }
  }
  .slick-prev{
    left: -90px;
  }
  .slick-next{
    right: -95px;
  }
  @include respond-to(xl){
    .slick-prev,.slick-next{
      display: none !important;
    }
  }
  @include respond-to(lg){
    padding: 5%;
  }
}

.containers{
  height: 500px;
  margin-bottom: 80px;
  overflow: hidden;
}
.section{
  height: auto;
}