
/// новости и блог
// сектион хежер
.header_section{
  @include between;
  &__title{
    @include responsiveText(28px,36px);
    display: inline-block;
    margin: 0;
    padding: 0;
  }
  &__link{
    display: inline-block;
    //margin-right: 20px;
    margin-top: 18px;
    text-decoration: underline;
    @include on-event(){color: var(--title_crl)}
    @include respond-to(md){
      margin-top: perc(17px);
    }
    @include respond-to(small){
      font-size: 14px;
    }
  }
  @include respond-to(sm){
    display: block;
    &__title{
      display: block;
      width: 100%;
    }
  }
}
.section--paddings{
  padding:30px 40px 40px;
  border: 1px solid #F1F1F2;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
  //padding-top: 30px;
  @include respond-to(md){
    padding: 5%;
  }
  @include respond-to(small){
    padding: 1%;
    border: none;
  }
}
.news-section{
  &__list{
    margin-top: 15px;
    @include media(10px){
      //padding-left: 15px;
      padding-top: 12px;
      margin-bottom: 10px;
      .media_img{
        width: 118px;
        height:118px;
      }
      .media_body{width: 100%}
      .news-section__body-title{
        font-weight: 600;
        @include text_size(20px,26px);
      }
      .news-section__body-content{
        background-color: $silver_bg;
        border-radius: 0px 100px 100px 0px;
        margin-top: 4px;
        padding: 15px 11px;
        margin-left: -11px;
        //margin-right: -15px;
        //@include flex($j:space-between,$w:'wrap');
        @extend .clearfix;
      }
      .news-section__body-date{
        margin-top: 5px;
        color: #B8BABB;
        font-weight: 500;
        @include text_size(12px,15px)
      }
      .list-content-text{
        @include text_size(16px,24px);
        max-width:65%;
        display: inline-block;
      }
      .list-content-button{
        float: right;
        width: 167px;
        height: 50px;
        line-height: 50px;
        font-size: 16px;
        &::after{
          top: 15px;
        }
      }
      @include respond-to(lg){
        .list-content-text{
          width: 100%;
          max-width: 100%;
        }
        .list-content-button{
          margin-top: 15px;
          float: left;
        }
        .news-section__body-content{
          background: none;
        }
      }
      @include respond-to(small){
        display: block;
        margin-bottom: 45px;
        .media_img{
          width: 100%;
          height: auto;
        }
        .news-section__body-content{
          background: none;
          border-radius:0;
        }
      }

    };
  }
  @include respond-to(lg){
    margin-bottom: 40px;
  }
}
// blogs
.blog-section__list{
  margin-top: 30px;
  .blog-section__title{
    font-weight: 600;
    margin-bottom: 15px;
    @include text_size(20px,26px)
  }
  .blog-section__text{
    @include text_size(16px,24px)
  }
  .blog-section__button{
    display:inline-block;
    position: relative;
    top: 21px;
    z-index: 2;
    margin: 0 auto;
    border-radius: 30px;
    padding: 6px;
    left: 12%;
    background-color: #fff;
    .button_wite{
      display: inline-block;
      width: 167px;
      height: 50px;
      line-height: 50px;
      font-size: 16px;
      &::after{
        top: 15px;
      }
    }
  }
  .blog-section__img{
    margin-top: -10px;
    width: 100%;
  }
  .blog-section__date{
    text-align: center;
    color: #B8BABB;
    font-weight: 500;
    margin-bottom: 13px;
    @include text_size(12px,15px);
  }
  // respon
  @include respond-to(lg){
    @include flex($dir:column);
    .blog-section__button{
      top: 0;
      left: 0;
      margin: 0;
      order: 1;
      margin-top: 20px;
    }
    .blog-section__img{
      order: -1;
      width: 250px;
      margin-bottom: 20px;
    }
    .blog-section__date{
      margin-top: 15px;
    }
  }
  @include respond-to(small){
    .blog-section__img{
      width: 100%;
    }
  }

}