/*********************
VARIABLES
**********************/
$link-color:#949899;
$link-hover:#CACBCC;
$link-vertical-padding:5px;      /*Controls Menu top & bottom padding*/
$link-horizontal-padding:25px;    /*Contols Menu left & right padding*/
$menu-background:'';        /*Control Menu Color Scheme*/
$menu-background-hover:'';



/*********************
NAVIGATION STYLES
**********************/

.menu_top {
  margin-top: 14px;
  margin-bottom: 10px;
  //margin-right: 5px;
  //@include span(3 no-gutter);
  ul {
    width:100%;
    list-style: none;
    padding: 0;
    margin: 0;
    @include flex($j:flex-end);
    //@include between;
    li {
      display:block;
      margin-left: 22px;
      a {
        display: block;
        color: #808687;
        //font-family: $semi;
        @include text_size(16px,22px);
        font-weight: 500;
        text-decoration: none;

        @include on-event() {
          color:$link-hover;
        }
        &:last-child{
          padding-right: 0;
        }
      }
      .active {
        font-weight: bold;
      }
      &:last-child{
        padding-right:0;
      }

    }
    @include respond-to(md){
      justify-content: center;
      li a{
        font-size: 14px;
      }
    }
    @include respond-to(small){
      li{
        margin-left: 11px;
      }
    }
  }
}


.main_menu{
  //max-width: susy-span(5);
  width: susy-span(5);
  margin: 49px 0;
  ul {
    width:100%;
    list-style: none;
    padding: 0;
    margin: 0;
    @include flex($j:center);
    //@include between;
    li {
      display:block;
      width: auto;
      margin-right:8%;
      a {
        display: block;
        color: $link-color;
        @include text_size(18px,22px);
        text-align: center;
        font-weight: normal;
        @include on-event() {
          color: $link-hover;
          text-decoration:none;
        }

      }
      .active {
        font-weight: bold;
      }
      &:last-child{
        margin-right: 0;
      }

    }
    .dpom_menu{
      position: relative;
      padding-right:25px;
      cursor: pointer;
      &::after{
        content: "";
        position: absolute;
        background: url("#{$base-url}img/icons/arr_botom.png") no-repeat;
        width: 14px;
        height: 8px;
        right: 0;
        top: 7px;
      }
    }
    .open{
      a{
        color: #485052 !important;
      }
      &::after{
        background: url("#{$base-url}img/icons/arr_up.png") no-repeat;
      }
    }

  }
  @include respond-to(lg){
    width: auto;
    order: 3;
    margin-top: 35px;
    margin-bottom: 25px;
    ul{
      display: none;
    }

  }
  @include respond-to(sm){
    margin-top: 13px;
    margin-bottom: 22px;
  }

}
// fixed


// mobide menu
.menu_drop{
  display: none;
  position:absolute;
  top: 120px;
  z-index: 999;
  width: 100%;
  max-width: 1110px;
  background: #FFFFFF;
  box-shadow: 0px 6px 6px rgba(0, 0, 0, 0.1);
  padding: 40px 25px;
  border-top: 2px solid #F1F1F2;
  //overflow: hidden;
  overflow-y: auto;
  //min-height: 300px;

  @media screen and (max-height: 700px) {
    max-height: 500px;
  }
  @media (max-width: 860px) and (max-height: 460px){
    //background: #949899;
    max-height:300px;
  }
  @media (max-width: 600px) and (max-height: 320px){
    //background: #949899;
    max-height:220px;
  }
  &::-webkit-scrollbar-button {
    background-image:url('');
    background-repeat:no-repeat;
    width:5px;
    height:0px
  }

  &::-webkit-scrollbar-track {
    background-color:#ecedee
  }

  &::-webkit-scrollbar-thumb {
    -webkit-border-radius: 0px;
    border-radius: 0px;
    background-color:#86CD62;
  }

  &::-webkit-scrollbar-thumb:hover{
    background-color:#86CD62;
  }

  &::-webkit-resizer{
    background-image:url('');
    background-repeat:no-repeat;
    width:4px;
    height:0px
  }

  &::-webkit-scrollbar{
    width: 4px;
  }

  // сам блок с тайтлом

  &__item{
    border-right: 2px solid #F1F1F2;
    .drop__item_title{
      @include text_size(22px,27px);
      color: #86CD62;
    }
  }
  .last_item{
    border: none;
  }
  // список из меню
  .drop__item_list{
    @extend %list;
    margin-top: 24px;
    li{
      @include flex();
      margin-bottom: 22px;
      @include on-event(){
        a{
          color: #485052;
          font-weight: normal;
          text-decoration: none;
        }
        svg{
          path{
            fill:#6ECCE6
          }
        }
      }
    }
      &--img{
        width: 28px;
        height: 28px;
      }
      &--link{
        display: block;
        margin-top: 5px;
        margin-left: 10px;
        font-weight: normal;
        color: #A5A9AB;
        @include text_size(16px,20px);
      }

  }
  // повдение при адаптиве
  @include respond-to(lg){
    top: 110px;
    min-width: 360px;
    max-width: 50%;
    left: 0;
    padding:20px 0;
    .menu_drop-full{
      display: none;
    }
    .vertical_menu{
      display: block;
    }

  }
  @include respond-to(sm){
    top: 80px;
    min-width: 320px;
    max-width: 80%;
  }
}
// мобильный акардион меню
.vertical_menu{
  display: none;
  height: 100%;
  //#mCSB_1_scrollbar_vertical{display: none !important;}
  //height: 500px;
   .accordeon{
     @extend %list;
     padding: 0 20px;
     .accordeon__head{
       margin: 0 0 2px 0;
       padding: 0;
       &--link{
         display: inline-block;
         font-weight: normal !important;
         @include text_size(18px,26px);
         height: 40px;
         line-height: 40px;
         padding-right: 20px;
         text-decoration: none;
         text-transform: uppercase;
         vertical-align: middle;
         width: 100%;
         color:var(--base-color);
         background: url("#{$base-url}img/icons/arr_botom.png") right center no-repeat;
       }
     }
     // акардеон под меню меню
     .accordeon__submenu{
       display: none;
       @extend %list;
       width: 100%;
       padding: 10px 0;
       .accordeon__submenu_link{
         @include flex();
         margin-bottom: 22px;
         padding: 5px 5px 5px 20px;
         text-decoration: none;
         @include on-event(){
           a{
             color: #485052;
             font-weight: normal;
             text-decoration: none;
           }
           svg{
             path{
               fill:#6ECCE6
             }
           }
         }
         &--img{
           width: 28px;
           height: 28px;
         }
         &--link{
           display: inline-block;
           margin-top: 5px;
           margin-left: 10px;
           font-weight: normal;
           color: #A5A9AB;
           @include text_size(16px,20px);
         }
       }

     }
     .accordeon__head.slide_menu,.accordeon__head.active{
       height: auto;
       .accordeon__submenu{
         display: block;
       }
       .accordeon__head--link{
         background-image:url("#{$base-url}img/icons/arr_up.png") ;
       }
     }

   }
}

// контакты в сплывающем меню
.vertical_menu_contact{
  background:url("#{$base-url}img/shadow_advantages.png") left top no-repeat, linear-gradient(180deg, #F6FAFC 0%, rgba(246, 250, 252, 0) 100%);
  margin-top: 30px;
  padding:10px 20px;
  @extend %list;
  li{
    @include flex;
    margin-top: 20px;
    section{
      width: calc(100% - 36px);
    }
    img{
      display: block;
      width: 16px;
      margin-right: 10px;
      margin-top: 5px;
    }
    a{
      display: block;
      color: #949899;
      font-weight: normal;
      margin-bottom: 10px;
    }
    span{
      color:  #949899;
      @include text_size(16px,20px)
    }
  }
}
// социалки в меню адаптив
.vertical_menu_social{
  margin-top: 15px;
  margin-left: 26px;
  margin-bottom: 20px;
  @include flex();
  a{
    display: inline-block;
    margin-left: 18px;
  }
}




