.vydel_section{
  margin-top: 50px;
  padding: 30px;
  background: #FFFFFF;
  border: 1px solid #F1F1F2;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.06);
  @include flex();
  .seting{
    flex-grow: 1;
    padding-right: 30px;
    //margin-left: 15px;
    border-right:2px solid #F1F1F2;
  }
  .zakaz{
    width: 200px;
    padding-left: 30px;
    .zakaz__butt{
      margin-top: 17px;
      width: 170px;
    }
  }
  @include respond-to(lg){
    flex-wrap: wrap;
    .seting{
      padding-bottom: 15px;
      flex-basis: 100%;
      border-right: 0;
      padding-right: 0;
      border-bottom:2px solid #F1F1F2;
    }
    .zakaz{
      width: 100%;
      float: right;
      @include flex();
      padding-left: 0;
      justify-content:space-between;

      .serch_box{
        min-width: 260px;
        margin-top: 20px;
        //margin-right: 30%;
      }
      .zakaz-box{
        display: inline-block;
        margin-top: 15px;
        margin-right: 20px;
      }
      .zz{
        @include flex();
      }
      .zakaz__butt{
        display:block;
        margin-top: 20px;
      }
    }
  }
  @include respond-to(md){
    .zakaz{
      @include flexbild(center);
    }
  }
  @include respond-to(small){
    .zakaz .zz{
      @include flexbild(center);
      .zakaz-box{
        margin-right: 0;
      }
    }
  }

}
// верхний блок с названием
.seting_serv{
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  padding-top: 7px;
  @include between;
  // верние название
  &__name{
    @include flex();
    max-width: 40%;
    &--logo{
      min-width: 60px;
      height: 60px;
      line-height: 60px;
      font-weight: 500;
      font-size: 20px;
      text-align: center;
      //padding-top: 19px;
      vertical-align: middle;
      color: #FFFFFF;
      margin-top: -7px;
      margin-right: 20px;
      border-radius: 100px;
      background-color: #6ECCE6;
    }
    &--name{
      font-weight: 500;
      font-size: 16px;
      line-height: 22px;
      margin-right: 10px;
    }
  }
  .ram{
    max-width: 15%;
  }
  .hdd{
    max-width: 20%;
    span{
      display: block;
    }
  }
  // с маркером и городом
  &--geo{
    max-width: 165px;
  }
  //
  .zahkvar{
    position: relative;
    top: -20px;
  }
  //
  &__mark{
    @include flex();
    span{
      margin-top: 1px;
      margin-left: 5px;
      font-weight: 500;
      font-size: 10px;
      line-height: 14px;
      color: #949899;
    }
  }
  //теги цветные
  &__tags{
    @include flex($w:wrap);
    a{
      display: inline-block;
      margin-top: 7px;
      color: #fff;
      padding:1px 8px;
      font-weight: 500;
      font-size: 10px;
      text-align: center;
      margin-right: 8px;
      &:nth-child(3){
        margin-right: 0;
      }
      @include on-event(){
        text-decoration: none;
      }
    }
    .tags--fiol{background-color: #768AD3}
    .tags--green{background-color: #96D376}
    .tags--red{background-color: #D37676}
    .tags--pink{background-color: #D376AE}
    .tags--blu{background-color: #6ECCE6}
  }
  @include respond-to(md){
    flex-wrap: wrap;
    justify-content: flex-start;
    &--geo.zahkvar{
      width: 100%;
      max-width: 100%;
      top: 0;
      margin: 15px 0;
      a{
        margin-right: 10px;
      }
    }
    &__name,article{
      flex-grow: 1;
    }
    &__name,.ram,.hdd{
      max-width: 50%;
    }
  }
  @include respond-to(sm){
    &__name{
      flex-grow: 2;
      width: 100%;
      margin-bottom: 20px;
    }
    &__name,.ram,.hdd{
      max-width: 100%;
    }

    article{
      flex-grow:0;
      margin-left: 80px;
    }
    article + article{
      margin-left: 20px;
    }
    &--geo.zahkvar{
      width: 280px;
      margin: 20px auto;
    }
  }
  @include respond-to(small){
    &__name--logo{
      display: none;
    }
    article{
      flex-grow: 1;
      margin-left: 0;
    }
  }
}
.ww{
  background: #fff;
  border: none;
  option{
    border: none;
    outline: none;
  }
}
// нижний с выпадающими и доменом
.seting_bot{
  @include between;
  .select_box{
    flex-grow: 1;
    @include between;
    section.mysec{
      width: 150px;
      margin-right: 30px;
      .vs__selected-options{
        height: 50px;
        color: #949899;
        background-position: right top 12px;
      }
      .vs__selected{
        padding-left: 0;
        padding-right: 15px;
        background-position: right top 12px;
      }
      .vs__dropdown-menu{
        padding: 0;
        padding-bottom: 10px;
        box-shadow: none;
        border: none;
      }
      .vs__dropdown-menu li{
        font-size: 12px;
        line-height: 15px;
        padding: 2px 0;
        @include on-event(){
          color: var(--base-color);
        }
      }
    }
  }
  @include respond-to(lg){
    .serch_box{
      display: none;
    }
  }
  @include respond-to(sm){
    display: block;
    .select_box{
      display: block;
      section{
        width: 280px;
        margin: 10px auto;
      }
    }
  }
}
.serch_box{
  flex-grow: 1;
  max-width: 290px;
  position: relative;
  .tooltip{
    position: absolute;
    right: -3px;
    top: 5px;
  }
}

/**
блок с права с селектом и кнопкой
 */
.zakaz .serch_box{
  display: none;
  @include respond-to(lg){
    display: block;
  }
}
.zakaz_select{
  .trigger{
    color: var(--title_crl);
    padding-right: 15px;
    //background-position: right top 12px;
  }
  .vs__selected{
    color: #949899;
  }
  .dropdownvisible{
    padding:0;
    padding-bottom: 10px;
    box-shadow: none;
    border: none;
  }
  .vs__dropdown-menu li{
    font-size: 12px;
    line-height: 15px;
    padding: 5px 0;
    @include on-event(){
      color: var(--base-color);
    }
  }
  .skidon{
    color: var(--title_crl);
    margin-left: 5px;
  }
  .vs__dropdown-menu{
    &:nth-child(3){
      a::after{
        content: "(-5%)";
        color: var(--title_crl);
        margin-left: 5px;
      }
    }
    &:nth-child(4){
      a::after{
        content: "(-10%)";
        color: var(--title_crl);
        margin-left: 5px;
      }
    }
    &:nth-child(5){
      a::after{
        content: "(-15%)";
        color: var(--title_crl);
        margin-left: 5px;
      }
    }
  }
}
.zakaz_select_sub{
  font-size: 14px;
  line-height: 30px;
  color: #949899;
  margin-top: -6px;
}